:root {
  --primary: #8f8f8f;
  --accent: #61dafb;
}
.box {
  position: relative;
  width: 380px;
  height: 420px;
  background: #303434;
  border-radius: 8px;
  overflow: hidden;
}
.box::before {
  content: "";
  position: absolute;
  top: -50%;
  left: -50%;
  width: 380px;
  height: 420px;
  background: linear-gradient(0deg, transparent, var(--accent), var(--accent));
  transform-origin: bottom right;
  animation: animate 3s linear infinite;
}
.box::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 380px;
  height: 420px;
  background: linear-gradient(
    180deg,
    transparent,
    var(--accent),
    var(--accent)
  );
  transform-origin: top left;
  animation: animate 3s linear infinite;
}

@keyframes animate {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.form {
  position: absolute;
  inset: 3px;
  background: #1c1c1c;
  z-index: 10;
  border-radius: 8px;
  padding: 50px 40px;
  display: flex;
  flex-direction: column;
}

.form h2 {
  color: var(--accent);
  font-weight: 500;
  text-align: center;
  letter-spacing: 0.1em;
}
.inputBox {
  position: relative;
  width: 300px;
  margin-top: 35px;
}
.inputBox input {
  position: relative;
  width: 100%;
  padding: 20px 10px 10px;
  background: transparent;
  border: none;
  outline: none;
  color: #1c1c1c;
  font-size: 1em;
  letter-spacing: 0.05em;
  z-index: 10;
}
.inputBox label {
  position: absolute;
  left: 0;
  padding: 20px 10px 10px;
  font-size: 1em;
  pointer-events: none;
  letter-spacing: 0.05em;
  transition: 0.5s;
  color: #d3d2d2;
}

.inputBox input:valid ~ label,
.inputBox input:focus ~ label {
  color: var(--accent);
  font-size: 0.75em;
  transform: translateX(-7px) translateY(-34px);
  font-weight: 500;
}
.inputBox i {
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 2px;
  background: #d3d2d2;
  pointer-events: none;
  transition: 0.5s;
}
.inputBox input:valid ~ i,
.inputBox input:focus ~ i {
  height: 44px;
  background-color: var(--accent);
  border-radius: 6px;
}

.links {
  display: flex;
  justify-content: space-between;
}

.links button {
  margin: 20px 0;
  text-decoration: none;
  color: #8f8f8f;
  font-size: 0.75em;
  font-weight: 600;
  transition: 0.3s;
  outline: none;
  background: transparent;
  border: none;
  margin-left: auto;
}

.links button:hover {
  color: var(--accent);
}
.join-btn {
  text-align: center;
  border: none;
  outline: none;
  background: var(--accent);
  padding: 11px 25px;
  border-radius: 4px;
  font-weight: 600;
  cursor: pointer;
  transition: box-shadow 0.5s;
}
.join-btn:hover {
  box-shadow: 0 0 15px 1px var(--accent);
}
.join-btn:active {
  opacity: 0.7;
}

