.multi-game-status{
    position: absolute;
    font-size: 1em;
    color:white;
    left: 20px;
    bottom: 20px;
    text-align: center;
    border: none;
    outline: none;
    background: var(--bg-light);
    padding: 8px 16px;
    border-radius: 4px;
    font-weight: 800;
    transition: box-shadow 0.5s;
  }