.navbar {
  display: flex;
  align-items: center;
  padding-inline: 20px;
  padding-block: 10px;
  background: var(--bg);
}
.navbar a {
  text-decoration: none;
  color: white;
  margin-left: 10px;
}
.nav-link-active {
  color: var(--accent);
}
.navbar img {
  height: 40px;
}
.brand {
  color: white;
}
.spacer {
  flex: 1;
}

.nav-link {
  font-size: 16px;
  color: white;
  font-weight: 500;
}
.active-nav-link {
  color: var(--accent);
}

.nav-leave-room-btn,.nav-start-game-btn {
  text-align: center;
  border: none;
  outline: none;
  margin-left: 10px;
  padding: 8px 11px;
  border-radius: 4px;
  font-weight: 600;
  cursor: pointer;
  transition: box-shadow 0.5s;
  font-size: 13px;
}
.nav-leave-room-btn{
  background: var(--accent);

}
.nav-start-game-btn{
  background: var(--accentPink);

}
.nav-leave-room-btn:hover {
  box-shadow: 0 0 15px 1px var(--accent);
}
.nav-leave-room-btn:hover {
  box-shadow: 0 0 15px 1px var(--accent);
}
.nav-leave-room-btn:active {
  opacity: 0.7;
}
