.home {
  color: white;
}
.home-heading {
  font-size: 3em;
}
.play-game-btn {
  background: transparent;
  color: var(--accent);
  font-size: 1.3em;
  font-weight: 600;
  margin-top: 30px;
  padding-block: 8px;
  padding-inline: 15px;
  border: 2px solid var(--accent);
  border-radius: 4px;
  box-shadow: inset 0px 0px 2px var(--accent);
  transition: all 0.4s;
}

.play-game-btn:hover {
  color: var(--bg);
  background: var(--accent);
  box-shadow: 0px 0px 15px 1px var(--accent);
}

/* 

  rotating cube
  
  */
  .cube-container {
    position: relative;
    width: 60px;
    height: 60px;
    transform-style: preserve-3d;
    transform: rotateX(-30deg);
    animation: cube-rotate 4s linear infinite;
  }
  
  
  @keyframes cube-rotate {
    100% {
      transform: rotateX(-30deg) rotateY(360deg);
    }
  }
  .cube-body {
    position: absolute;
    width: 100%;
    height: 100%;
    transform-style: preserve-3d;
    top: 0;
    left: 0;
  }
  .cube-wall {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(#6a03fa, #c403fa);
    transform-style: preserve-3d;
  }
  .cube-wall:nth-child(1) {
    transform: rotateY(calc(90deg * var(--cube-rt-1))) translateZ(-30px);
  }
  .cube-wall:nth-child(2) {
    transform: rotateY(calc(90deg * var(--cube-rt-2))) translateZ(-30px);
  }
  .cube-wall:nth-child(3) {
    transform: rotateY(calc(90deg * var(--cube-rt-3))) translateZ(-30px);
  }
  .cube-wall:nth-child(4) {
    transform: rotateY(calc(90deg * var(--cube-rt-4))) translateZ(-30px);
  }
  .cube-top {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: #6a03fa;
    transform-style: preserve-3d;
    transform: rotateX(90deg) translateZ(30px);
  }
  .cube-top:before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: #c403fa;
    transform-style: preserve-3d;
    transform: rotateZ(90deg) translateZ(-60px);
  }
  .cube-top:after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: #c403fa8f;
    transform-style: preserve-3d;
    transform: rotate(90deg) translateZ(-70px);
    filter: blur(20px);
  }
  