.main {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: calc(100vh - 67px);
  background-color: var(--bg);
  text-align: center;
}
.pnf-error {
  display: flex;
  justify-self: center;
  align-items: center;
  flex-direction: column;
  font-size: 3rem;
  color: white;
}
.pnf-code {
  font-size: 3em;
  color: orangered;
  font-weight: bold;
}
.pnf-message {
  font-size: 2.5rem;
}
