.game-loader-div{
    display: block;
}
.game-loading {
  color: var(--accentPink);
}
.game-loader {
  display: flex;
  max-width: 300px;
  min-width: 80vw;
  height: 50px;
}
.game-loader-child {
  display: flex;
  /* height: 100%; */
  animation: game-loading 5.2s ease-out;
  background-color: #ff2c84;
  color:var(--accent);
  font-size:13px;
  padding:10px;
}
@keyframes game-loading {
  0% {
    width: 0%;
  }
  100% {
    width: 100%;
  }
}
