* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  margin: 0;
  font-family: "Poppins", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

:root {
  --accent: #45f3ff;
  --accentPink: #ff0099;
  --bg: #303434;
  --bg-light: #606a6a;
  --nav-bg: #262637;
  --cube-rt-1: 0;
  --cube-rt-2: 1;
  --cube-rt-3: 2;
  --cube-rt-4: 3;
}
