.grid {
  display: grid;
  top: 20px;
  left: 20px;
  grid-template-columns: 100px 100px 100px;
  justify-content: center;
  align-items: center;
}
.square {
  display: flex;
  border: 1px solid black;
  width: 100px;
  height: 100px;
  justify-content: center;
  align-items: center;
}
.single-game-status{
  position: absolute;
  font-size: 1em;
  color:white;
  left: 20px;
  bottom: 20px;
  text-align: center;
  border: none;
  outline: none;
  background: var(--bg-light);
  padding: 8px 16px;
  border-radius: 4px;
  font-weight: 800;
  transition: box-shadow 0.5s;
}

.grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  width: 300px;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
  overflow: hidden;
  border-radius: 50px;
  box-shadow: 4px 4px 10px rgb(0, 0, 0.01),
    -3px -3px 10px rgba(255, 255, 255, 0.484);
  opacity: 1;
  transition: opacity 1s linear;
  border-left: 1px solid white;
  border-top: 1px solid white;
}
.square {
  background: transparent;
  width: 100px;
  height: 100px;
  border: none;
  color: rgba(0, 255, 255, 0.9);
  font-size: 2.5em;
  font-weight: 900;
}

.grid .square:nth-child(5) {
  border: 1px solid rgba(0, 255, 255, 0.9);
}
.grid .square:nth-child(1),
.grid .square:nth-child(4),
.grid .square:nth-child(7) {
  border-right: 1px solid rgba(0, 255, 255, 0.9);
}

.grid .square:nth-child(3),
.grid .square:nth-child(6),
.grid .square:nth-child(9) {
  border-left: 1px solid rgba(0, 255, 255, 0.9);
}
.grid .square:nth-child(1),
.grid .square:nth-child(2),
.grid .square:nth-child(3) {
  border-bottom: 1px solid rgba(0, 255, 255, 0.9);
}
.grid .square:nth-child(7),
.grid .square:nth-child(8),
.grid .square:nth-child(9) {
  border-top: 1px solid rgba(0, 255, 255, 0.9);
}
.grid .square:nth-child(4),
.grid .square:nth-child(6) {
  border-top: 1px solid rgba(0, 255, 255, 0.9);
  border-bottom: 1px solid rgba(0, 255, 255, 0.9);
}
.grid .square:nth-child(2),
.grid .square:nth-child(8) {
  border-left: 1px solid rgba(0, 255, 255, 0.9);
  border-right: 1px solid rgba(0, 255, 255, 0.9);
}