.player-container {
  display: flex;
  padding-block: 50px;
  background: #00000000;
  transition: all 0.7s;
  border-radius: 50%;
  display: flex;
  align-items: center;
}
.player-box {
  position: relative;
  width: 150px;
  height: 150px;
  background: #303434;
  border-radius: 50%;
  overflow: hidden;
  margin-inline: 50px;
  transition: all 0.5s;
}
.player-box:nth-child(2) {
  animation: appear 0.4s linear;
}
.player-box::before {
  content: "";
  position: absolute;
  top: -50%;
  left: -50%;
  width: 150px;
  height: 150px;
  background: linear-gradient(0deg, transparent, var(--accent), var(--accent));
  transform-origin: bottom right;
  animation: animate 3s linear infinite;
}
.player-box::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 150px;
  height: 150px;
  background: linear-gradient(180deg, transparent, var(--accent), var(--accent));
  transform-origin: top left;
  animation: animate 3s linear infinite;
}

@keyframes animate {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
@keyframes appear {
  0% {
    transform: translateX(-250px);
  }
  100% {
    transform: translateX(0px);
  }
}
.player-content {
  position: absolute;
  inset: 3px;
  background: #524f4f;
  z-index: 10;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--accent);
  font-size: 1.3em;
  font-weight: 600;
}
.start-game-btn {
  text-align: center;
  border: none;
  outline: none;
  background: var(--accent);
  padding: 11px 25px;
  border-radius: 4px;
  font-weight: 600;
  cursor: pointer;
  transition: box-shadow 0.5s;
}
.room-info {
  position: absolute;
  font-size: 13px;
  color:white;
  left: 20px;
  bottom: 20px;
  text-align: center;
  border: none;
  outline: none;
  background: var(--bg-light);
  padding: 8px 16px;
  border-radius: 4px;
  font-weight: 800;
  transition: box-shadow 0.5s;
}
.room-info:hover {
  background: var(--accent);
  color: #1c1c1c;
  box-shadow: 0 0 20px 1px var(--accent);
}
